.qcs-button {
    width: 120px;
    height: 45px;
    border: 1px solid rgba(0, 0, 0, .7);
    border-radius: 2.5px;
    transition: all 0.5s cubic-bezier(0.28, 0.11, 0.32, 1);
    -webkit-transition: all 0.5s cubic-bezier(0.28, 0.11, 0.32, 1);
    overflow: hidden;

    &:hover {
        border: 1px solid rgba(0, 0, 0, .1); }

    &:hover > a {
        color: #fff; }


    &:hover > span {
        height: 43px;
        background: #000; }

    a {
        font-family: "Helvetica Neue", "Roboto", sans-serif;
        font-size: 20px;
        font-weight: 300;
        text-align: center;
        text-decoration: none;
        color: #000;
        position: relative;
        z-index: 1;
        display: block;
        width: 100%;
        height: 100%;
        line-height: 2;
        transition: color .25s cubic-bezier(0.28, 0.11, 0.32, 1);
        -webkit-transition: color .25s cubic-bezier(0.28, 0.11, 0.32, 1); }

    span {
        width: 118px;
        height: 0;
        background: none;
        display: block;
        position: relative;
        top: -43px;
        float: left;
        border-radius: 2.5px;
        transition: height .25s cubic-bezier(0.28, 0.11, 0.32, 1), background-color .25s cubic-bezier(0.28, 0.11, 0.32, 1);
        -webkit-transition: height .25s cubic-bezier(0.28, 0.11, 0.32, 1), background-color .25s cubic-bezier(0.28, 0.11, 0.32, 1); } }
